	module.exports = {"data":{"markdownRemark":{"html":"<h1>Restricting API usage JS web api</h1>\n<p>Eating good food is fun.\nI thought it'll be fun to catalogue all of the places I've been eating at the past couple years.\nJogging down the memory lane is something I find really relaxing.</p>\n<p>What do I hope to achieve from this?</p>\n<p>To see the map please go here:\nScreenshot!</p>\n<p>How can I do it?\nI decided to open a Yelp account and begin dumping my memory as reviews.\nThe reason as to why I chose to document on Yelp</p>\n<p>(as opposed to simply pin-ing places on Google maps list) is to share my experiences and tips and tricks that I learned for some of my fave eateries.</p>\n<p>Also it'll be nice to generate some statistics as to which area in Toronto I ate the most, average food prices, and categories.</p>\n<p>I was hoping to be able to simply query to their endpoint and get all of my reviews.\nMuch to my surprise, Yelp <strong>DOES NOT OFFER</strong> a clean way to scrape user or business reviews.\nIt does however, offer a way to search for business.</p>\n<p>Time for a plan b.</p>\n<p>In regards to places with other branches/franchise locations, I just simply chose one that I remember the most of.</p>\n<p>Required a lot of manual intervention.</p>\n<p>Would I continue this again?\nProbably not, its too time consuming to manually update places I've been to. Also I was late„Å™„ÅÆ„ÄÇ</p>\n<p>‰ª•‰∏ä„Åß„Åô‚ô™</p>","frontmatter":{"title":"üçù Mini Foodie","date":"September 25, 2017","language":"english","categories":"side-writing","helper":null}}},"pathContext":{"slug":"/posts/mini-foodie/"}}

